/* 🌟 Variables Globales - Palette de Couleurs */
:root {
    --primary-color: #5D9CEC;
    --secondary-color: #FF8C42;
    --accent-color: #E74C3C;
    --background-color: #F5F5F5;
    --text-color: #333;
    --light-gray: #eaeaea;
    --shadow-color: rgba(0, 0, 0, 0.15);
}

/* 🌍 Style Global */
body {
    font-family: 'Poppins', sans-serif;
    background-color: var(--background-color);
    color: var(--text-color);
    margin: 0;
    padding: 0;
    line-height: 1.6;
    overflow-x: hidden;
}

/* 🌟 Conteneur Principal */
.container {
    max-width: 1200px;
    margin: auto;
    padding: 20px;
}

/* 🌟 Conteneur Global */
section {
    padding: 40px 0;
}

/* 🌟 Conteneur Principal */
header.container {
    background: linear-gradient(to bottom, rgba(93, 156, 236, 0.2), rgba(255, 140, 66, 0.2));
    padding: 60px 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0px 4px 10px var(--shadow-color);
}

header h1 {
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--primary-color);
}

header p.lead {
    font-size: 1.2rem;
    font-weight: 500;
}

/* 🌟 Carrousel des Besoins des Utilisateurs */
.user-needs-carousel {
    max-width: 800px;
    margin: auto;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 4px 10px var(--shadow-color);
}

.carousel-image {
    width: 100%;
    height: 350px;
    object-fit: cover;
    border-radius: 10px;
}

.carousel-caption {
    background: rgba(0, 0, 0, 0.6);
    padding: 15px;
    border-radius: 10px;
}

.carousel-caption h5 {
    font-size: 1.3rem;
    font-weight: bold;
    color: white;
}

.carousel-caption p {
    font-size: 1rem;
    color: white;
}

/* 📌 Carrousel Navigation */
.carousel-control-prev-icon,
.carousel-control-next-icon {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
}

/* 🌟 Section Hero */
.hero-section {
    width: 100%;
    height: 300px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.2);
}

.hero-image {
    width: 100%;
    height: auto;
    object-fit: cover;
}

/* 🌟 Section Fonctionnalités */
.features-section .card {
    border-radius: 10px;
    padding: 20px;
    transition: transform 0.3s ease-in-out;
    background: white;
    box-shadow: 0px 4px 10px var(--shadow-color);
}

.features-section .card:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 20px var(--shadow-color);
}

.feature-icon {
    width: 80px;
    height: 80px;
    margin-bottom: 15px;
    border-radius: 10px;
}

/* 🌟 Boutons */
.btn-primary {
    background: var(--primary-color);
    color: white;
    border: none;
    padding: 12px 20px;
    font-weight: bold;
    border-radius: 6px;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}

.btn-primary:hover {
    background: var(--secondary-color);
    box-shadow: 0px 4px 10px var(--shadow-color);
}

/* 🌟 Pied de Page */
footer {
    background: var(--primary-color);
    color: white;
    padding: 20px;
    text-align: center;
    border-top: 5px solid var(--secondary-color);
}

.footer-logo {
    width: 100px;
    height: auto;
    margin-bottom: 10px;
}

.btn-group .btn {
    margin: 5px;
    border-radius: 20px;
    font-weight: bold;
}

/* 📱 Responsive Design */
@media (max-width: 768px) {
    .carousel-image {
        height: 250px;
    }
    
    header h1 {
        font-size: 2rem;
    }

    header p.lead {
        font-size: 1rem;
    }

    .features-section .card {
        padding: 15px;
    }

    .footer-logo {
        width: 80px;
    }

    .btn-primary {
        padding: 10px 15px;
        font-size: 0.9rem;
    }
}
